<template>
  <div class="home">
        <!-- Loading Indicator -->
    <div v-if="isLoading" class="loading-overlay">
      <div class="spinner"></div>
    </div>
    <!-- 仅在第一步显示标题 -->
    <h1 v-if="step === 1" class="step-one-title">Please choose a recognition mode</h1>
    <!-- 第一步：选择模式 -->
    <div v-if="step === 1" class="step-one-container step-module">
      <!-- 添加 Instructions 部分 -->
      <div class="instructions">
        <h2>Instructions</h2>
        <p>Please select one of the following modes:</p>
        <ul>
          <li><strong>Cell encapsulation:</strong> Detect, analyse, and count the number of droplets with encapsulated cells. You can select the "Single-cell encapsulated" option to perform an analysis.</li>
          <li><strong>Double emulsion:</strong>Detect, analyse, and count both double emulsions and single droplets.</li>
          <li><strong>Single droplet:</strong>Detect, analyse, and count individual droplets.</li>
        </ul>
      </div>
      <div class="step-one-option" @click="selectMode('Cell encapsulation')" style="position: relative;">
        <span>Cell encapsulation</span>
        <div class="checkbox-container" @click.stop>
          <input type="checkbox" v-model="isSingleCellEncapsulated" id="single-cell-checkbox" />
          <label for="single-cell-checkbox">Single-cell encapsulated</label>
        </div>
      </div>
      <div class="step-one-option" @click="selectMode('Double emulsion')">Double emulsion</div>
      <div class="step-one-option" @click="selectMode('Single droplet')">Single droplet</div>
    </div>
    <!-- 第二步：选择算法 -->
    <!-- 仅在第二步显示标题 -->
    <h1 v-if="step === 2 && selectedMode === 'Single droplet'" class="step-one-title">Please choose an algorithm</h1>
    <div v-if="step === 2 && selectedMode === 'Single droplet'" class="step-two-container step-module">
       <!-- 添加 Instructions 部分 -->
      <div class="instructions">
        <h2>Instructions</h2>
        <p>Please select an algorithm based on your analysis needs:</p>
        <ul>
          <li><strong>Deformable DETR:</strong> A deep learning algorithm that delivers superior accuracy and flexibility for object detection across a wide range of scenarios.</li>
          <li><strong>Edge detection:</strong> A traditional image recognition method that offers faster detection speeds but may sacrifice some accuracy.</li>
        </ul>
      </div>
      <div class="step-one-option" @click="selectAlgorithm('Deformable DETR')">Deformable DETR</div>
      <div class="step-one-option" @click="selectAlgorithm('Edge detection')">Edge detection</div>
      <button @click="goBack" class="back-button">Back</button>
    </div>
    <!-- 第三步：上传图片或视频 -->
    <div v-if="step === 3 || (step === 2 && selectedMode !== 'Single droplet')">
      <h1 class="step-three-title">Please upload an image or video</h1>
      <div class="modules-container">
        <!-- 文件选择输入框和自定义按钮 -->
        <div class="module">
          <!-- 添加 Instructions 部分 -->
          <div class="instructions">
             <h2>Instructions</h2>
            <p>Please follow the steps below to upload your file:</p>
            <ul>
              <li>Click the <strong>"Select an image or video"</strong> button to choose your file.</li>
              <li>After selecting, confirm by clicking <strong>"Upload"</strong>.</li>
              <li>Ensure your image has a resolution of at least <strong>1000×1000</strong> and is smaller than <strong>3MB</strong> in size.</li>
              <li>For videos, please ensure a resolution of at least <strong>1000×1000</strong> and a size smaller than <strong>10MB</strong>.</li>
            </ul>
          </div>
          <input type="file" accept="image/bmp,image/jpeg,image/jpg,image/png,image/tiff,image/tif,video/mp4,video/avi,video/webm" @change="onFileChange" class="file-input" ref="fileInput" />
          <div class="content">
            <div class="button-container">
              <button @click="triggerFileInput">Select an image or video</button>
              <button @click="uploadFile">Upload</button>
            </div>
            <p v-if="selectedFile">Selected file: {{ selectedFile.name }}</p>
            <div class="images-container" v-if="imageUrl || processedImageUrl">
              <div v-if="imageUrl" class="image-wrapper">
                <h3>Original File:</h3>
                <img v-if="isImage" :src="imageUrl" alt="Original File" class="preview-image" />
                <video v-if="isVideo" :src="imageUrl" controls class="preview-video"></video>
                <p v-if="fileInfo">{{ fileInfo }}</p>
              </div>
            </div>
            <button @click="goBackFromUpload" class="back-button">Back</button>
            <button @click="analyzeFile" class="analysis-button" :disabled="!isUploaded">Analysis</button>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'ImageRecognition',
  data() {
    return {
      step: 1, // 当前步骤
      selectedMode: null, // 用户选择的模式
      selectedAlgorithm: null, // 用户选择的识别算法（仅适用于Single droplet模式）
      selectedFile: null, // 保存用户选择的文件
      imageUrl: null, // 保存图片预览的URL
      fileInfo: null, // 保存文件信息（分辨率和文件大小）
      processedImageUrl: null, // 保存处理后的图片或视频URL
      uploadedFilename: null, // 保存上传的文件名
      isImage: false, // 标识文件是否为图片
      isVideo: false, // 标识文件是否为视频
          // 其他数据
      isSingleCellEncapsulated: false, // Single-cell encapsulated 默认不勾选
      isUploaded: false, // 上传状态，默认为未上传
      isLoading: false, // 加载状态，默认为未加载

    };
  },
  methods: {
    // 选择模式
    selectMode(mode) {
      if (mode === 'Cell encapsulation') {
        // 根据复选框是否勾选决定 selectedMode
        if (this.isSingleCellEncapsulated) {
          this.selectedMode = 'Single-cell encapsulated';
        } else {
          this.selectedMode = 'Cell encapsulation';
        }
      } else {
        this.selectedMode = mode; // 对于其他模式，直接设置 selectedMode
      }

      // 根据所选模式调整步骤
      if (mode === 'Single droplet') {
        this.step = 2; // 进入算法选择步骤
      } else {
        this.step = 3; // 直接进入上传步骤
      }

      // 输出 selectedMode 以供调试
      console.log('Selected mode:', this.selectedMode);
    },
    // 选择识别算法
    selectAlgorithm(algorithm) {
      this.selectedAlgorithm = algorithm;
      this.step = 3; // 进入上传步骤
            // 输出选中的算法
      console.log('Selected Algorithm:', this.selectedAlgorithm);
    },
    // 返回上一步
    goBack() {
      this.step = 1; // 返回选择模式步骤
    },
    // 上传界面返回上一步encapsulated
    goBackFromUpload() {
      if (this.selectedMode === 'Single droplet') {
        this.step = 2; // 返回选择算法步骤
      } else {
        this.step = 1; // 返回选择模式步骤
      }
    },
    // 触发文件选择输入框
    triggerFileInput() {
      this.$refs.fileInput.click();
    },
    // 处理文件选择事件
    onFileChange(event) {
      this.selectedFile = event.target.files[0]; // 将选择的文件保存到data属性
      this.isUploaded = false; // 重置上传状态
      if (this.selectedFile) {
        // 获取文件类型和大小
        const fileType = this.selectedFile.type;
        const fileSize = this.selectedFile.size;

        // 校验文件格式是否支持
        const validImageTypes = ['image/bmp', 'image/jpeg', 'image/jpg', 'image/png', 'image/tiff', 'image/tif'];
        const validVideoTypes = ['video/mp4', 'video/avi', 'video/webm'];
        const isValidFormat = validImageTypes.includes(fileType) || validVideoTypes.includes(fileType);

        // 校验文件大小（图片小于3MB，视频小于10MB）
        const maxSize = fileType.startsWith('image') ? 3 * 1024 * 1024 : 10 * 1024 * 1024;
        const isValidSize = fileSize <= maxSize;

        // 如果文件格式不正确
        if (!isValidFormat) {
          alert('Invalid file format. Please select a valid image or video format.');
          this.selectedFile = null;
          return;
        }

        // 如果文件大小超出限制
        if (!isValidSize) {
          const sizeLimit = fileType.startsWith('image') ? '3MB' : '10MB';
          alert(`File size exceeds the limit. The maximum allowed size is ${sizeLimit}.`);
          this.selectedFile = null;
          return;
        }

        // 如果格式和大小都正确，继续处理文件
        this.imageUrl = URL.createObjectURL(this.selectedFile); // 创建文件预览的URL
        const fileTypeCategory = fileType.split('/')[0];
        this.isImage = fileTypeCategory === 'image';
        this.isVideo = fileTypeCategory === 'video';

        // 获取文件的分辨率
        if (this.isImage) {
          const img = new Image();
          img.onload = () => {
            const width = img.width;
            const height = img.height;
            const size = (this.selectedFile.size / 1024).toFixed(2); // 文件大小（KB）
            this.fileInfo = `Resolution: ${width}x${height}, Size: ${size} KB`;
          };
          img.src = this.imageUrl;
        } else if (this.isVideo) {
          const video = document.createElement('video');
          video.onloadedmetadata = () => {
            const width = video.videoWidth;
            const height = video.videoHeight;
            const size = (this.selectedFile.size / 1024).toFixed(2); // 文件大小（KB）
            this.fileInfo = `Resolution: ${width}x${height}, Size: ${size} KB`;
          };
          video.src = this.imageUrl;
        }
      }
    },
    // 上传文件
    async uploadFile() {
      if (!this.selectedFile) {
        alert('Please select a file first'); // 如果没有选择文件，提示用户
        return;
      }

      const formData = new FormData();
      formData.append('file', this.selectedFile);
      formData.append('mode', this.selectedMode);
      if (this.selectedMode === 'Single droplet') {
        formData.append('algorithm', this.selectedAlgorithm);
      }

      // 显示加载指示器
      this.isLoading = true;

      try {
        const response = await axios.post('/api/upload', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
          timeout: 60000,  // 1分钟超时
        });

        alert(`File "${this.selectedFile.name}" is successfully uploaded!`);
        console.log(response.data);

        this.uploadedFilename = response.data.file_path;
        this.isUploaded = true;

      } catch (error) {
        if (error.response) {
          // 后端返回的错误
          if (error.response.status === 400) {
            alert('Upload failed: Invalid file format or size.');
          } else {
            alert(`Upload failed: ${error.response.data.message}`);
          }
        } else {
          // 其他网络错误
          alert('Upload failed: Please check your network connection.');
        }
      } finally {
        this.isLoading = false;
      }
    },

    // 分析文件
    analyzeFile() {
      if (this.isImage || this.isVideo) {
        const routeName = this.isImage ? 'ImageAnalysis' : 'VideoAnalysis';  // 根据文件类型决定路由

        this.$router.push({
          name: routeName,
          query: {
            selectedMode: this.selectedMode,
            selectedAlgorithm: this.selectedAlgorithm,
            filename: this.uploadedFilename,
          },
        });
      } else {
        alert('Please select a valid file first.');
      }
    }
  },

};
</script>

