import { createRouter, createWebHistory } from 'vue-router'
import HomePage from '@/components/HomePage.vue'
import MachineIdentification from '@/components/MachineIdentification.vue'
import OtherModule from '@/components/TransferLearning.vue'
import ImageAnalysis from '@/components/ImageAnalysis.vue'
import VideoAnalysis from "@/components/VideoAnalysis.vue";
const routes = [
  {
    path: '/',
    name: 'HomePage',
    component: HomePage,
    props: true

  },
  {
    path: '/machine-identification',
    name: 'MachineIdentification',
    component: MachineIdentification,
    props: true
  },
  {
    path: '/transfer-learning',
    name: 'TransferLearning',
    component: OtherModule
  },
  {
    path: '/image-analysis',
    name: 'ImageAnalysis',
    component: ImageAnalysis,
    props: true
    },
  {
    path: '/video-analysis',
    name: 'VideoAnalysis',
    component: VideoAnalysis,
    props: true
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
